body {
  font-size: $font-size-base;
  line-height: $line-height-base;
  font-family: $font-family-sans-serif;
}

a:focus {
  outline: 1px dotted
}

hr {
  border: 0 #ccc solid;
  border-top-width: 1px;
  clear: both;
  height: 0
}

h1 {
  font-size: 25px
}

h2 {
  font-size: 23px
}

h3 {
  font-size: 21px
}

h4 {
  font-size: 19px
}

h5 {
  font-size: 17px
}

h6 {
  font-size: 15px
}

ol {
  list-style: decimal
}

ul {
  list-style: disc
}

li {
  margin-left: 30px
}

p, dl, hr, h1, h2, h3, h4, h5, h6, ol, ul, pre, table, address, fieldset {
  margin-bottom: 20px
}